.section-deploy {
  background: $color-white;
}

.deploying-animation {
  position: absolute;
  top: 50%;
  left: 0;
  height: 2px;
  width: 100%;
  overflow: hidden;
  z-index: -1;
}

.deploying-animation:before {
  position: absolute;
  content: '';
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background: $color-grey;
  @media screen and (max-width: 760px) { display: none; }
}

.deploying-animation:after {
  position: absolute;
  content: '';
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
  width: 20px;
  height: 2px;
  background: $color-brand;
  animation: deploying-motion 6s infinite;
  @media screen and (max-width: 760px) { display: none; }
}

.hosting-logos {
  position: relative;
  margin: 4rem 0 2rem 0;
}

.hosting-logos > .container {
  display: flex;
  z-index: 1;
  justify-content: center;
  flex-wrap: wrap;

  .hosting-logo {
    flex: 0 0 50%;
    height: 8rem;
    padding: 1rem;
    margin: -1px;
    background: rgba(255,255,255,0.8);
    border: 1px solid $color-grey;
    position: relative;

    @media screen and (min-width: 790px) { flex: 0 0 25%; }

  }

  .hosting-logo img,
  .hosting-logo svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

}

@keyframes deploying-motion {
  0% {
    left: -20%;
    width: 20px;
  }
  10% {
    left: -20%;
    width: 20px;
  }
  50% {
    left: 50%;
    width: 160px;
  }
  90% {
    left: 120%;
    width: 20px;
  }
  100% {
    left: 120%;
    width: 20px;
  }
}
