.section-roots {
  background: $color-dark;
  color: $color-white;
}

.section-roots p {
  color: $color-grey;
  opacity: .8;
}

.section-roots .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.no-vendor-lock {
  margin-top: 4rem;

  .no-vendor-lock--logos {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin: 1.5rem -1rem 0 -1rem;
  }

  .no-vendor-lock--logo {
    height: 1.25rem;
    margin: .75rem;

    &:last-of-type {
      margin-right: 0;
    }

    &.small {
      height: 1rem;
    }

    &.big {
      height: 1.5rem;
    }

  }

}

.content-illustration {
  margin-top: 2rem;
  transition: transform 1s ease;
  position: relative;
  height: 400px;
  width: 700px;
}

.content-illustration-one, .content-illustration-two {
  position: absolute;
  max-height: 100%;
  border-radius: 6px;
}

.content-illustration-one {
  top: 0;
  left: 0;
  box-shadow: 16px 16px 20px rgba(0, 0, 0, 0.1);
}

.content-illustration-two {
  top: 2rem;
  right: 0;
  height: 260px;
  box-shadow: 16px 16px 20px rgba(0, 0, 0, 0.1);
}

.content-illustration[data-emergence=hidden] {
  transform: translateY(-5rem);
}

.content-illustration[data-emergence=visible] {
  transform: translateY(0);
}
