html, body {
  font-family: 'Avenir', 'Helvetica Neue', Helvetica, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 400;
}

a { font-weight: 500; }

p { color: $color-copy; }

.section--title { margin-bottom: 2rem; }

.page-title {
  font-size: 3rem;
  font-weight: 600;
  margin-bottom: .5rem;
}

.page-subtitle {
  margin: 1rem auto 0 auto;
  max-width: 500px;
}

h3 {
  font-weight: 600;
  font-size: 1rem;
  margin-bottom: .5rem;
}

.section--title {
  font-size: 2rem;
  font-weight: 600;
  line-height: 1.12;
}

.section--sub p {
  margin-top: 1rem;
  max-width: 500px;
}

.section--sub.is-center {
    margin: 0;
    @media screen and (min-width: 700px) { margin: 0 auto; }
}
