  .shortcode-tip,
  .shortcode-warning,
  .shortcode-error,
  .shortcode-disclaimer {
    background: $color-grey;
    padding: 1rem 1rem 1rem 2rem;
    position: relative;

    h4 {
      text-transform: uppercase;
    }

    p {
      margin: 0 0 2rem 0 !important;
      padding: 0 !important;
    }

    p:first-of-type {
      margin: 0 !important;
    }

    &:before {
      /* Set the circle size here */
      $circle-size: 1.5rem;

      position: absolute;
      text-align: center;
      line-height: $circle-size;
      color: $color-white;
      font-weight: 600;
      content: 'ℹ︎';
      left: -calc($circle-size / 2);
      top: 1rem;
      background: $color-brand;
      width: $circle-size;
      height: $circle-size;
      border-radius: calc($circle-size / 2);
      z-index: 5;
    }

    &:after {
      position: absolute;
      left: -0.1rem;
      top: 0;
      height: 100%;
      width: 0.2rem;
      background: $color-brand;
      content: '';
      z-index: 0;
    }

  }

  .shortcode-warning {
    &:before {
      content: '!';
      background: color($color-merino saturation(+ 50%) w(- 50%));
    }

    &:after {
      background: color($color-merino saturation(+ 50%) w(- 50%));
    }
  }

  .shortcode-error {
    &:before {
      content: '!';
      background: $color-error;
    }

    &:after {
      background: $color-error;
    }
  }

  .shortcode-disclaimer {
    &:before {
      content: '?';
      background: $color-sinbad;
    }

    &:after {
      background: $color-sinbad;
    }
  }