.hero {
  padding-top: 5rem;
  min-height: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 1;
  background: $color-white;
  overflow: hidden;

  .hero--text-block {
    padding: 9rem 0 0 0;
    text-align: left;

    @media screen and (min-width: 1000px) {
      padding: 80px 0 100px 0;
    }

  }

  .hero--title {
    font-size: 2rem;
    line-height: 1;
    font-weight: 400;
    
    @media screen and (min-width: 520px) {
      font-size: 3rem;
    }
  }

  .hero--sub {
    max-width: 460px;
    margin-top: 2rem;
    font-size: 1rem;
    font-weight: 500;
    opacity: .8;
  }

  .hero-cta {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 2.5rem;

    @media screen and (min-width: 520px) { margin-top: 3rem; }

    a:not(.button) {
      color: $color-medium-dark;
      font-style: italic;
      font-weight: 500;
      position: relative;
      margin-left: 2rem;
      cursor: pointer;

      transition: opacity .15s ease;

      &:hover {
        opacity: 0.5;
      }
    }

    .launch-demo {
      display: none;
      color: $color-medium-dark;
      font-style: italic;
      font-weight: 500;
      position: relative;
      margin-left: 2rem;
      cursor: pointer;

      @media screen and (min-width: 520px) { display: block; }

    }

    .launch-demo::before {
      position: absolute;
      content: '';
      left: -1rem;
      top: 50%;
      width: 1px;
      height: 3.75rem;
      background: #f0f0f0;
      transform: translateY(-50%);
    }

  }

  @media screen and (min-width: 1000px) {
    flex-direction: row;
  }

}

.hero--content {
  width: 100%;
  margin-top: -5rem;
  position: relative;
}

.factory-video {
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  width: 110%;
  max-width: none;
  transform: translate(-50%, 0);
  pointer-events: none;
  height: auto;
  z-index: -10;

  @media screen and (min-width: 600px) {
    top: 50%;
    left: 50%;
    width: 100%;
  }

  @media screen and (min-width: 820px) {
    top: 20%;
    left: 50%;
    width: 100%;
  }

  @media screen and (min-width: 1000px) {
    top: 45%;
    left: auto;
    right: -60px;
    width: 70%;
    transform: translate(0, -45%);
  }

  @media screen and (min-width: 1200px) {
    top: 45%;
    left: auto;
    right: 0;
    width: 70%;
    transform: translate(0, -45%);
  }

}

.built-for {
  position: absolute;
  left: 8vw;
  bottom: 2rem;
  font-size: .65rem;
  font-weight: 600;
  letter-spacing: .1em;
  text-transform: uppercase;
  opacity: 1;
}

.built-for--logos {
  display: flex;
  align-items: center;
  margin-top: .5rem;
}

.built-for--logo {
  margin-right: 1rem;
}
