/* Config */
$sidebar-width: 320px;

.header.docs .search {
  position: relative;
  margin-right: 2rem;

  &::after {
    position: absolute;
    content: '';
    left: 0.7rem;
    top: 0.65rem;
    height: 100%;
    width: 2rem;
    background-image: url('/img/search.svg');
    background-size: 0.9rem;
    background-repeat: no-repeat;
    z-index: 10;
    opacity: 0.25;
  }

  input { padding-left: 2rem; }

}


.sidebar {
  position: fixed;
  top: 4rem;
  left: 0;
  width: $sidebar-width;
  height: calc(100% - 4rem);
  padding: 2rem 2rem 2rem 4rem;
  background: $color-white;
  overflow: auto;
  font-size: 0.86rem;
  z-index: 10;
  transform: translateX(-$sidebar-width);
  transition: transform 0.15s linear;

  h2 { margin-bottom: 2rem; }

  h3 {
    font-size: 1rem;
    margin-bottom: 0.5rem;
  }

  ul {
    margin-bottom: 2rem;
    color: $color-copy-dark;
  }

  .sidebar--table-of-contents {
    margin-left: 0;
    padding-left: 1rem;
    border-left: 2px solid $color-grey;
  }

  li {
    line-height: 1.4;
    margin-bottom: 0.5rem;

    a {
      color: $color-copy-dark;
      text-decoration: none;
      font-weight: 400;

      &.active {
        color: $color-brand;
        font-weight: 600;
      }

    }
  }

  @media screen and (min-width: 860px) {
    transform: translateX(0);
  }

}

.docs-content {
  position: relative;
  padding-top: 5.5rem;
  margin: 0 auto;
  max-width: 600px;
  min-height: 100vh;
  background: $color-white;
  color: $color-dark;
  font-size: 0.86rem;
  line-height: 1.6;
  word-spacing: 0.05em;

  .container {
    padding: 0 2rem;
  }

  .pagination {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    font-weight: 600;
    line-height: 1em;
    margin: 4rem 0 2rem 0;
    color: $color-brand;

    span {
      position: relative;
      display: block;
    }

    span:before {
      position:absolute;
      content: '';
      top: 50%;
      transform: translateY(-50%);
      height: 1em;
      width: 1em;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
    }

    a:first-of-type { margin-right: 4rem; }

    .prev-doc { padding-left: 1.5rem; }
    .prev-doc:before {
      left: 0;
      background-image: url('/img/chevron-left.svg');
    }

    .next-doc { padding-right: 1.5rem; }
    .next-doc:before {
      right: 0;
      background-image: url('/img/chevron-right.svg');
    }

  }

  @media screen and (min-width: 860px) {
    margin-left: $sidebar-width;
  }

  @media screen and (min-width: 1240px) {
    margin: 0 auto;
  }

}

/* Content styling */
.docs-content {
  padding-bottom: 4rem;

  h1 { font-size: 2rem; }
  h2 {
    font-size: 1.5rem;
    margin-top: 2rem;
  }
  h3 {
    font-size: 1rem;
    margin-top: 2rem;
  }

  strong { font-weight: 600; }

  p {
    color: $color-copy-dark;
    margin-top: 1rem;
    padding-bottom: 1rem;
  }

  a {
    font-weight: 600;
    color: $color-brand;
    text-decoration: none;
  }

  blockquote {
    position: relative;
    padding-left: 1rem;
    font-style: italic;
    margin: 1rem 0;
  }

  blockquote::before {
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    height: 100%;
    width: 2px;
    background: $color-brand;
  }

  img { max-width: 100%; }

  /* Inline code */
  code {
    font-family: 'Roboto Mono', monospace;
    font-size: 0.75rem;
    background: $color-light-grey;
    color: $color-brand;
    font-weight: 500;
    padding: 0.15em 0.5em;
  }

  pre code { color: $color-dark; }
}
