$header-height: 5rem;

.header {
  position: fixed;
  z-index: 20;
  width: 100%;
  height: $header-height;
  background-color: rgba(255, 255, 255, 0.95);
  border-bottom: 1px solid $color-grey;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .left-block {
    height: 100%;
    width: 120px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    transition: all .15s linear;
    overflow: hidden;

    .logo {
      line-height: 0;
      background-image: url(../img/forestry-full.svg);
      background-repeat: no-repeat;
      background-position: left center;
      background-size: 100% 100%;
      width: 100%;
      height: 1.5rem;
      overflow: hidden;
      transition: width .15s linear, opacity .15s ease;
      backface-visibility: hidden;

      &:hover {
        opacity: 0.6;
      }
    }

    @media screen and (max-width: 700px) {
      width: 1.5rem;
      .logo { background-image: url(../img/forestry-symbol.svg); }
    }

  }

  .right-block {
    display: flex;
    align-items: center;
  }

}

.header.dark {
  color: $color-white;
  background-color: $color-dark;
  border-bottom: 1px solid $color-dark;

  .logo { background-image: url('../img/forestry-neg-full.svg'); }

  .nav {
    color: $color-white;
    &.expanded { color: $color-dark; }
  }

  .button { color: $color-white; }

  @media screen and (max-width: 700px) {
    .logo { background-image: url(../img/forestry-neg-symbol.svg); }
  }

}

.header.docs {
  height: $header-height;

  .nav {

    &.expanded {
      display: block;
      height: 100%;
      overflow: auto;
      -webkit-overflow-scrolling: touch;
      padding: 1.24rem 2rem 2rem 2rem;

      @media screen and (min-width: 780px) { padding: 1.21rem 4rem 2rem 4rem; }

      ul.main-nav-items {
        width: 100%;
        border-bottom: 1px solid $color-grey;
        padding-bottom: 2rem;
      }

      li { font-size: 1rem; }

      .docs-nav-items {
        font-size: 1rem;
        margin-top: 2rem;
        padding-top: 2rem;
        border-top: 1px solid $color-grey;
        width: 100%;

        h2 { margin-bottom: 2rem; }

        h3 {
          font-size: 1rem;
          margin-bottom: 0.5rem;
        }

        ul {
          margin-bottom: 2rem;
          color: $color-copy-dark;
        }

        li {
          line-height: 1.4;
          margin-bottom: 0.5rem;

          a {
            color: $color-copy-dark;
            text-decoration: none;
            font-weight: 400;

            &.active {
              color: $color-brand;
              font-weight: 600;
            }

          }
        }

      }

      .signup-button {
        margin: 2rem 0 0 0;
      }

      .mini-nav--logo {
        display: block;
      }

    }

    @media screen and (min-width: 780px) {
      display: none;
    }

    @media screen and (min-width: 860px) {
      .docs-nav-items { display: none; }
    }

    @media screen and (min-width: 1100px) {
      display: block;
    }

  }

  .mini-nav--toggle {
    @media screen and (min-width: 780px) { display: block; }
    @media screen and (min-width: 1100px) { display: none; }
  }

}

.header.docs.container {
  padding: 0 2rem;
  @media screen and (min-width: 780px) { padding: 0 4rem; }
}

.nav {
  color: $color-dark;
  display: none;
  position: relative;
  transition: all .15s linear;
  white-space: nowrap;

  &.expanded {
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    background: $color-white;
    z-index: 30;
    width: 100%;
    height: auto;
    padding: 1.72rem 8vw 2rem 8vw;
    border-bottom: 1px solid $color-grey;

    .nav-content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 100%;
    }

    li {
      display: block;
      font-size: 1.5rem;
      line-height: 1.8;
    }

    .signup-button {
      margin: 2rem 0 0 0;
    }

    .mini-nav--logo {
      display: block;
    }

    @media screen and (min-width: 780px) {
      display: none;
    }

  }

  .nav-content {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  @media screen and (min-width: 780px) {
    display: block;
  }

}

.nav li {
  display: inline;
  list-style: none;
  margin-right: 1.5rem;
  &:last-child { margin: 0; }

  .nav-item {
    color: inherit;
    text-decoration: none;
    font-weight: 500;
    transition: color .15s ease;

    &:hover { color: $color-brand; }
    &.active { color: $color-brand; }
  }

}

.mini-nav--toggle {
  display: block;
  position: relative;
  z-index: 40;
  cursor: pointer;

  img {
    display: block;
    width: 1.2rem;
  }

  .toggle-open { display: block; }
  .toggle-close { display: none; }

  @media screen and (min-width: 780px) { display: none; }

}

.nav.expanded + .mini-nav--toggle {
  .toggle-open { display: none; }
  .toggle-close { display: block; }
}

.mini-nav--logo {
  display: none;
  line-height: 0;
  background-image: url(../img/forestry-full.svg);
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 100% 100%;
  width: 110px;
  height: 1.5rem;
  overflow: hidden;
  transition: all .15s linear;
  backface-visibility: hidden;
  margin-bottom: 2rem;
}
