.section-trusted {
  padding: 4rem 0;
  background: $color-light-grey;

  .trusted--row-1 {
    display: none;
    align-items: baseline;

    h3 {
      flex: 0 0 25%;
      @media screen and (min-width: 800px) { flex: 0 0 40%; }
    }

    span {
      max-width: 400px;
      font-size: 16px;
      font-style: italic;
      opacity: .8;
      margin-right: 2rem;
    }

    .text-button {
      margin-left: auto;
      align-self: baseline;
      white-space: nowrap;
    }

    @media screen and (min-width: 700px) {
      display: flex;
    }

  }

  .trusted--row-2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    user-select: none;
    pointer-events: none;

    .trusted--logo {
      height: 40px;
      &.smaller {
        height: 40px;
      }
    }

    @media screen and (min-width: 700px) {
      margin-top: 60px;
    }

  }

}
