/* Base button styling */

.button {
  display: inline-block;
  font-size: 16px;
  text-align: center;
  font-weight: 500;
  line-height: 1em;
  padding: 1rem 2rem;
  border-radius: .15em;
  border: 1px solid transparent;
  background-color: $color-grey;
  cursor: pointer;
  transition: all .3s cubic-bezier(.694, 0, .335, 1);

  /* Style */
  &.primary {
    color: $color-white;
    background-color: $color-brand;
    &:hover { opacity: .8; }
  }

  &.secondary {
    color: $color-dark;
    background-color: transparent;
    border: 1px solid $color-brand;
    &:hover {
      color: $color-white;
      background-color: $color-brand;
    }
  }

  /* Button sizes */
  &.small {
    font-size: 14px;
    padding: .5rem 1.25rem;
    &.rounded { border-radius: 1.5rem; }
  }
  &.big {
    padding: 1.25rem 2rem;
    &.rounded { border-radius: 1.5rem; }
  }

}

.text-cta {
  margin-top: 30px;
}

.text-button {
  font-size: 16px;
  padding: 0 0 .25em 0;
  background-color: transparent;
  border-bottom: 1px solid currentColor;
  transition: border .3s cubic-bezier(.694, 0, .335, 1);
  text-decoration: none;
  &:hover {
    opacity: .8;
  }
}
