.section-sync {
  background: $color-white;
}

.publish-button-animation {
  position: absolute;
  top: 3%;
  right: 3%;
  width: 8%;
  height: 4%;
  border-radius: 3px;
  background: $color-brand;
  animation: publish-button-flash 12s infinite;
}

.git-push-animation {
  position: absolute;
  top: 5%;
  right: 1.4%;

  .commit {
    font-size: 50%;
    font-weight: 500;
    background: $color-dark;
    color: $color-white;
    padding: .5rem 1rem;
    border-radius: 3px;
    animation: commit-movement 12s infinite;
    transform-origin: center;
  }

}

@keyframes publish-button-flash {
  0% { background: $color-brand; transform: scale(1); }
  19% { background: $color-brand; transform: scale(1); }
  21% { background: $color-brand-light; transform: scale(1.2); }
  23% { background: $color-brand; transform: scale(1); }
  100% { background: $color-brand; transform: scale(1); }
}

@keyframes commit-movement {
  0% {
    transform: translateY(0) scale(0);
    opacity: 0;
    box-shadow: 0 0px 0px 0px rgba(0,0,0, 0.15);
    filter: blur(0);
  }
  20% {
    transform: translateY(0) scale(0);
    opacity: 0;
    box-shadow: 0 0px 0px 0px rgba(0,0,0, 0.15);
    filter: blur(0);
  }
  50% {
    transform: translateY(40%) scale(1);
    opacity: 1;
    box-shadow: 0 6px 20px -2px rgba(0,0,0, 0.15);
    filter: blur(0);
  }
  70% {
    opacity: 0;
  }
  100% {
    transform: translateY(120%) scale(0.2);
    opacity: 0;
    box-shadow: 0 0px 0px 0px rgba(0,0,0, 0.15);
    filter: blur(20px);
  }
}
