.footer {
  padding: 4rem 0 0 0;
  font-size: .8rem;
  color: $color-dark;
  background: $color-white;
  letter-spacing: .025em;

  a {
    text-decoration: none;
    line-height: 1.8;
    transition: opacity .15s ease;
    will-change: opacity;
    vertical-align: top;

    &:hover { opacity: .6; }

  }

  .footer-title {
    display: block;
    margin-bottom: .5rem;
    font-weight: 600;
  }

  .slack-logo {
    margin-top: 1rem;
  }

  .column { overflow: hidden; }

  .column:nth-child(1) {
    flex: 0 0 100%;
    @media screen and (min-width: 700px) { flex: 0 0 100%; }
    @media screen and (min-width: 1000px) { flex: 0 0 20%; }
  }

  .column:nth-child(2) {
    flex: 0 0 50%;
    @media screen and (min-width: 700px) { flex: 0 0 25%; }
    @media screen and (min-width: 1000px) { flex: 0 0 20%; }
  }

  .column:nth-child(3) {
    flex: 0 0 50%;
    @media screen and (min-width: 700px) { flex: 0 0 25%; }
    @media screen and (min-width: 1000px) { flex: 0 0 20%; }
  }

  .column:nth-child(4) {
    flex: 0 0 100%;
    @media screen and (min-width: 700px) { flex: 0 0 50%; }
    @media screen and (min-width: 1000px) { flex: 0 0 40%; }
  }

}

.lower-footer {
  border-top: 1px solid $color-grey;
  padding: 2rem 0;

  > .container {
    display: flex;
    justify-content: space-between;
  }

  ul {
    display: flex;
    align-items: center;
  }

  li {
    margin-left: 1rem;
  }
}
