pre {
  padding: 0.5rem 1rem;
  background: $color-light-grey;
}

.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: $color-light-grey;
  font-family: 'Roboto Mono', monospace;
  font-size: 0.75rem;
  font-weight: 500;
}

.hljs,
.hljs-subst {
  color: $color-dark;
}

.hljs-comment {
  color: $color-medium-dark;
}

.hljs-keyword,
.hljs-attribute,
.hljs-selector-tag,
.hljs-meta-keyword,
.hljs-doctag,
.hljs-name {
  color: $color-copy;
}

.hljs-type,
.hljs-string,
.hljs-number,
.hljs-selector-id,
.hljs-selector-class,
.hljs-quote,
.hljs-template-tag,
.hljs-deletion {
  color: $color-brand;
}

.hljs-title,
.hljs-section {
  color: $color-dark;
  font-weight: 600;
}

.hljs-regexp,
.hljs-symbol,
.hljs-variable,
.hljs-template-variable,
.hljs-link,
.hljs-selector-attr,
.hljs-selector-pseudo {
  color: $color-dark;
}

.hljs-literal {
  color: $color-brand;
}

.hljs-built_in,
.hljs-bullet,
.hljs-code,
.hljs-addition {
  color: $color-brand;
}

.hljs-meta {
  color: $color-sinbad;
}

.hljs-meta-string {
  color: $color-sinbad;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: 600;
}
