.blog-list-wrapper > .container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 8rem;
  background: $color-white;

  @media screen and (min-width: 800px) { flex-direction: row; }
}

.blog-header {
  flex: 0 0 40%;
  padding: 0 0 4rem;
  > * { max-width: 400px; }

  @media screen and (min-width: 700px) { padding: 0 0 4rem; }

  .blog-header--description {
    color: $color-copy;
    font-size: 0.95rem;
    line-height: normal;
    margin-top: 0.5rem;
  }

  .newsletter-signup {
    display: none;
    margin-top: 4rem;
    @media screen and (min-width: 800px) { display: block; }
  }

  .newsletter-signup--text {
    font-size: 0.95rem;
  }

  .newsletter-signup--field {
    display: flex;
    flex-wrap: nowrap;
    margin-top: 1rem;

    input {
      border-radius: 1.5em 0 0 1.5em;
      width: 80%;
      padding: 1.25rem;
      font-size: 1rem;
    }

    .button {
      border: 1px solid $color-brand;
      border-radius: 0 1.5em 1.5em 0;
    }

  }

}

.section-blog-list {
  flex: 0 0 60%;
  padding: 0;

  @media screen and (min-width: 800px) { padding: 0 0 0 4rem; }

  .blog-post {
    margin-bottom: 6rem;
    max-width: 600px;
  }

  .post-title {
    font-size: 2.5rem;
    line-height: 1.25;
    margin-bottom: 0.5rem;
  }

  .post-meta {
    text-transform: uppercase;
    font-size: 0.8rem;
    margin-bottom: 0.5rem;
  }

  .post-date {
    font-weight: 600;
    color: $color-medium-dark;
  }

  .post-category {
    font-weight: 600;
    margin-left: 0.5rem;

    &[data-category="Forestry"] { color: $color-brand; }
    &[data-category="Jekyll"] { color: #ffcf55; }
    &[data-category="Hugo"] { color: #54DEFD; }

  }

}
